<template>
  <div class="register">
    <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
    <mt-field label="手机" placeholder="请输入手机号" type="number" v-model="phone"></mt-field>
    <mt-field label="QQ" placeholder="请输入QQ" type="number" v-model="QQ"></mt-field>
    <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
    <mt-button type="danger" @click="sub">提交</mt-button>
  </div>
</template>

<script>
  import { MessageBox } from 'mint-ui';
  export default {
    data(){
      return {
        username:'',
        phone:'',
        QQ:'',
        password:''
      }
    },
    methods:{
      sub(){
       if(this.username && this.phone && this.QQ && this.password){
         this.$store.state.aa.people[this.name] =this.phone;
         this.$store.state.aa.people[this.psw] =this.password;
         MessageBox({
           title: '',
           message: '注册成功',
           confirmButtonText:'确定'
         }).then(action => {
          this.$router.push('/login');
         });
       }else{
         MessageBox({
           title: '',
           message: '请输入正确信息',
           confirmButtonText:'确定'
         });
       }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.mint-cell-wrapper{
  text-align: left;
}
</style>



