            txt: '如果说掌握一门赖以生计的技术是技术人员要学会的第一课的话， 那么我觉得技术人员要真正学会的第二课，不是技术，而是业务、交流与协作，学会关心其他工作伙伴的工作情况和进展。',
            html_text:'<h4 class="atitle">程序员请放下你的技术情节，与你的同伴一起进步</h4>' +
            '<p>如果说掌握一门赖以生计的技术是技术人员要学会的第一课的话， 那么我觉得技术人员要真正学会的第二课，不是技术，而是业务、交流与协作，学会关心其他工作伙伴的工作情况和进展。</p>' +
            '<p>为什么这么说呢？ 因为技术人员太容易陷入&ldquo;孤岛&rdquo;状态，更注重自己的工作任务的完成，忽视其他工作伙伴的工作，甚至一无所知。 我就一直犯这样的错误。我敢说，对内心我还是比较明白清楚的，但是对外面所发生的事情实在是知之甚少，这不是好的状态。 一个开明、开放的程序员不应该囿于自己狭隘的小天地，而是更广阔地去看待工作和职业，和同伴一起进步和成功。</p>' +
            '<p><strong>为什么要关心业务？</strong></p>' +
            '<p>很多技术人员都立志成为系统架构师， 那是编程领域的&ldquo;圣杯级职业&rdquo;。问题是， 系统架构本身是为了业务需求和扩展而服务的，必须充分理解业务需求和未来的发展趋势，深入理解系统所涉及的数据及分布， 才能作出更可靠的设计决策。如果对业务知之甚少，以为仅靠书上说的那一套，或者以前的零碎经验，就能够胜任的话，那这个项目多半要失败的。 因此，即使立志要成为系统架构师，也要对业务有很深的理解。</p>' +
            '<p>其次， 技术的发挥必须有用武之地。如果没有足够强度和有挑战性的业务需求和扩展， 足够多的问题的磨炼，技术的提升又从何谈起？ 仅仅靠阅读那些技术书籍吗？ 那只能提供一个指导的作用罢了；真正还是要在实战中得到提高。</p>' +
            '<p>如果一个技术人员对业务不感兴趣，只对技术感兴趣， 那会发生什么事情呢？ 他将只能满足于使用自己所熟悉的技术去完成上面分派下来的任务； 也许今天是做一个系统A， 明天是做另外一个类似的系统B。 他对系统涉及的各种业务都不甚了解，也不清楚行业领域的发展状况， 就只能局限于使用技术来做各种具体的功能，无法提出中肯的建议。他的职业发展将严重耦合于所掌握的技术。如果该技术保持比较长久的生命力，那么， 他还能兵来将挡水来土淹， 但无论如何，也只能局限于成为这门技术的&ldquo;高级工程师&rdquo;而已；如果该技术开始被淘汰，那就悲剧了。</p>' +
