            '<p>你知道吗？ 三国时期，我最佩服的是徐元直先生。 他是一个技术和业务同样精通的人才； 技术方面， 作战思想丰富， 业务方面， 实际指挥作战和应变能力都很强， 这样的人才， 难怪即使不献一策， 曹操这样的人物也愿意留他在帐下， 让他无功受禄，&mdash;&mdash; 能够不让他为竞争对手所用就已经是大功一件了。</p>' +
            '<p><strong>放下技术情结</strong></p>' +
