            '<p>是的， 你学了 C, java, python , Lisp , ...., 还可以列出更长，你还想掌握并发编程，软件架构等等，你的同事却只会C， java 或 python , 会敲入一些命令， 这样你就舒服了吗？ 感到优越了吗？ 高枕无忧了吗？ 成为核心骨干了吗？</p>' +
            '<p>技术是学无止境的， 一个人的精力却有限，就算一个人在某个领域里非常精通，那么同时也可能意味着这个人在其它方面是孤陋寡闻，&ldquo;技术牛人&rdquo; 和 &ldquo;科学大师&rdquo; 一样， 可能只是一个美妙的光环，是程序员给自己套上的铁链。不要妄想一揽子全抓在手里， 确立自己的专长， 善于与别人优势互补，良好协作才是。敢于舍弃 &ldquo;技术情结&rdquo; 给自己带来的安全感， 才能走出更广阔的空间。</p>' +
            '<p><strong>技术人员的通病</strong></p>' +
