            '<p>大多数技术人员，包括我在内，都还不懂得主动与工作伙伴良好协作，只是因为工作关系而不自觉地交流和协作。不信，在你工作之余的时候，你会主动邀请别人一起来编程，共同去做一个有用的产品， 还是埋头去学习技术，孤立地去做一些技术实验，来掌握所谓的某门开发技术？</p>' +
            '<p>一个技术人员对于另一个技术人员的认可往往起始于对其技术能力的是否认可。技术人员面试的时候通常会更关注： 他掌握了哪些技术？ 对这些技术的掌握程度如何？ 而不是这个人利用自己的技术能力和所学做了哪些影响卓著的事情。这就导致了： 技术人员往往更关注自己的技术能力的发展，而忽视其他能力的培养。</p>' +
            '<p>因此，大多数技术人员，包括我在内， 都有四个通病： 一，不清楚自己究竟能够利用计算机做什么，不懂得如何充分利用计算机的真正威力； 二. 不能主动与别人优势互补和良好协作； 三。放不下技术情结； 四. 局限于狭隘的自我优越感，以及伴随而生的强迫症。这四个通病导致一个人永远局限于只见一叶不见林的狭隘视角。</p>' +
            '<p><strong>真正优秀的技术人才</strong></p>' +
            '<p>作为一名技术人员，你对公司的业务发展能够提出自己的建议吗？ 你有能力说服工作伙伴和主管来采纳你的提议吗？ 能够集思广益，融众人之所长吗？ 我认为，一个真正优秀的技术人才，应当具备这种多方面的能力，除了技术专长，他具备说服能力，集思广益， 能够有力地推动事情的发展。企业也应当提供机会，让技术人员参与更广阔的工作，而不仅仅是写代码和完成需求功能。</p>' +
            '<p>我相信，从企业聘用人才的角度来说，它们更看重一名技术人才究竟产生了怎样的影响。只是当大量技术人员没有什么可证明自己做过有影响力的事情的情况下，才不得不求其次，去聘用那些在技术能力上有更高造诣的技术人员； 从技术人员的角度来说，因为他没有什么可以证明自己确实做过有影响力的事情，只好倚靠有限的技术水平为自己寻一个差强人意的安身之所。我觉得， 那些没有做过有影响力的事情的技术人员，相比那些利用自己所学做过有价值的事情的人来说，其职业高度就已经低了一个档次，尽管后者可能在技术能力上要差一些。</p>' +
            '<p>事实上，我敢断言： 中国不缺技术能力优秀的人才，虽然技术级别与国外还差一两个档次， 但还是人才济济的。确实有很多人依然努力成为最好的技术人才，致力于引进国外先进技术。中国缺少的是那些懂得利用计算机能力创造价值、服务和效益的技术人才。 这种技术人才必须能够放下技术情结，以更开放、开明的心态去与其他的人良好协作，优势互补， 一个真正优秀的产品往往是多个人共同协作完成，孤胆英雄的年代已经过去。即便是一个黑客，如果他能与另一个黑客强强联合， 也能产生出比自己一个人更强大的能量。要知道，苹果至少也是由乔布斯和沃兹尼亚克两个人初创起来的。</p>' +
            '<p><strong>要一起进步和成功</strong></p>' +
            '<p>最后， 如果一个技术人员通过重重磨难终于成为了很牛级的人物，而他的亲友早已埋没于尘世中，过着一般的日子， 他的成功又有多大意义呢？ 应该聚合所有人的才智和经验，一起创造共赢的结局才对，缺了谁都不行。人生苦短，正是因为一路上有了亲友的陪伴， 才不会寂寞， 人生才不至于苍白。不要只顾着自己一个人往前走路， 把亲友们抛在看不见的后面。</p>'
          },
          {
            title: '每人的生命里都有一只碗',
            logo: require('../assets/images_jiuye/share02.png'),
            txt: '有一个年轻人去买碗，来到店里他顺手拿起一只碗，然后依次与其它碗轻轻碰击，碗与碗之间相碰时立即发出沉闷、浑浊的声响，他失望地摇摇头。',
            html_text:'<h4 class="atitle">每人的生命里都有一只碗</h4>' +
            '<p>有一个年轻人去买碗，来到店里他顺手拿起一只碗，然后依次与其它碗轻轻碰击，碗与碗之间相碰时立即发出沉闷、浑浊的声响，他失望地摇摇头。 然后去试下一只碗&hellip;&hellip;他几乎挑遍了店里所有的碗，竟然没有一只满意的，就连老板捧出的自认为是店里碗中精品也被他摇着头失望地放回去了。</p>' +
            '<p>老板很是纳闷，问他老是拿手中的这只碗去碰别的碗是什么意思？他得意地告诉老板，这是一位长者告诉他的挑碗的诀窍，当一只碗与另一只碗轻轻碰撞时，发出清脆、悦耳声响的，一定是只好碗。老板恍然大悟，拿起一只碗递给他，笑着说：&ldquo;小伙子，你拿这只碗去试试，保管你能挑中自己心仪的碗&rdquo;。</p> ' +
            '<p><img src="../../static/img/share02.png" alt="每人的生命里都有一只碗" width="90%"/></p> <p>他半信半疑地依言行事。奇怪！他手里拿着的每一只碗都在轻轻地碰撞下发出清脆的声响，他不明白这是怎么回事，惊问其详。</p> <p>老板笑着说，道理很简单，你刚才拿来试碗的那只碗本身就是一只次品，你用它试碗那声音必然浑浊，你想得到一只好碗，首先要保证自己拿的那只也是只好碗&hellip;&hellip;</p> <p>就像一只碗与另一只碗的碰撞一样，一颗心与另一颗心的碰撞，需要付出真诚，才能发出清脆悦耳的响声。自己带着猜忌、怀疑甚至戒备之心与人相处，就难免得到别人的猜忌与怀疑。其实每个人都可能成为自己生命中的&ldquo;贵人&rdquo;，前提条件是，你应该与人为善。</p> ' +
            '<p>你付出了真诚，就会得到相应的信任，你献出爱心，就会得到尊重。反之，你对别人虚伪、猜忌甚至嫉恨，别人给你的也只能是一堵厚厚的墙和一颗冷漠的心。每个人的生命里都有一只碗，碗里盛着善良、信任、宽容、真诚，也盛着虚伪、狭隘、猜忌、自私&hellip;&hellip;请剔除碗里的杂质，然后微笑着迎接另一只碗的碰撞，并发出你们清脆、爽朗的笑声吧！</p> ' +
            '<p>做最好的自己，才能碰见最好的别人。</p> '
          },
