            txt: '看到昔日好友发了一篇日志《咎由自取》他说他是一个悲观者，感觉社会抛弃了他，脾气、性格在6年的时间里变化很大，很难适应这个社会。',
            html_text:'<h4 class="atitle">犯错了怎么办？</h4>' +
            '<p>看到昔日好友发了一篇日志《咎由自取》他说他是一个悲观者，感觉社会抛弃了他，脾气、性格在6年的时间里变化很大，很难适应这个社会。人生其实就是不断犯错的过程，在这个过程中不断的犯错，不断的吸取教训，不断的成长。也许日子里的惊涛骇浪，不过是人生中的水花摇晃，别用显微镜放大你的悲伤。</p> <p><span style="color: rgb(0, 0, 0);"><strong>龙背墙的传说</strong></span></p>' +
            '<p>龙背墙是由孙红雷、郭富城、余男、邓家佳主演的《全民目击》里面的一个传说，关于一个父爱和救赎的神话。影片讲的是一个富二代因为酒后撞死人，父亲为了挽救女儿 ，策划了一系列伪证，哪怕最后替女儿坐牢。&ldquo;什么都不重要，只要她好，就好。&rdquo;&ldquo;我愿用一生年华，换她十五年光阴。&rdquo;&ldquo;你若是父亲，也一定会这样做。&rdquo;这个故事 ，让我理解了很多，这电影让人感同身受之处在于，从一开始，就没有完美的结局，总有人会犯错会受伤，总有人会去承担去背负。</p> ' +
            '<p>传说龙背墙不是一面墙，而是一座山。南海龙王老来得子，小龙王淘气任性，到处惹祸。直到有一天，他闯了大祸，南海龙王为了救儿子，他冒替小龙王，趴在盘龙山下， 接受雷电的击打。眼看父亲被烧的遍体鳞伤，奄奄一息，小龙王悔恨愧疚，他一下冲出来，要承担这个惩罚，龙王为了阻止儿子，便一头撞向身旁的金刚壁，当场死去。&ldquo; 养不教，父之过&ldquo;，南海龙王认为自己死得其所，死后，他的尸体便化作龙背山，经过这场灾难之后，小龙王幡然醒悟，终生恪守本分，与人为善。后人将龙背山改称为龙背墙，是因为这面墙，挡住了小龙王所有的罪行，故此，后人称为龙背墙。&nbsp;</p> ' +
            '<p><span style="color: rgb(0, 0, 0);"><strong>物是人非事事休</strong></span></p> <p><strong>物也非，人也非，事事非，往日不可追。</strong>记得小时候我带弟弟去自家鱼塘钓鱼，拿着鱼竿，拿着桶，我很熟练轻松的跨过那不到半米的沟渠，还没来得及我提醒他，只听&ldquo;扑通&rdquo;一声，我回头，弟弟已经掉进了鱼塘，我站在原地不知所措，大声的哭喊。幸好我爸在家听见我哭，问我怎么回事，我说弟弟掉下去了，据我爸回忆说，当时就看到鱼塘冒一圈圈的水，衣服都来不及脱，大冬天的直接下去捞人，由于泥太多，我爸只能托举着我弟身体等到人来救援...救上来后，因为呛着水、做人工呼吸、放牛背、盖被子..所有人都绝望的认为活不了的时候，弟弟却奇迹般的醒了，虽说是我爸救了他，但是我也有很大的责任，要是我俩不去钓鱼，又或者是他走在前面，就不会发生这样的事情，现在想想还很后怕，如果不是那一刻的哭喊，如果不是我爸即时赶到，如果不是有路人帮忙，我怕这辈子都会内疚下去....还记得有一次弟弟吃了马蹄莲的花蕊，后来因为花蕊中毒，嘴又黑又肿进了医院，每次回忆这事儿的时候我老笑话他，可不曾想，他说是我骗他说那花蕊好吃...哈哈，反正我是不记得了。</p> ' +
            '<p><span style="color: rgb(255, 0, 0);"><strong>写在最后：</strong></span></p> <p>犯错不需要勇气，你甚至有很多理由来解释来弥补，但是却需要很大的勇气来面对，必须得面对，很现实的面对。 '
          },
          {
            title: '两只蜗牛艰难又浪漫的一吻',
            logo: require('../assets/images_jiuye/share04_1.jpg'),
            txt: '这是国外一摄影师拍摄到的，看到这一幕，还真有爱！于是想查查蜗牛是哪科的，百度科普了一下，原来蜗牛是蜗牛是陆生贝壳类软体动物，关键是蜗牛雌雄同体，异体交配，雌雄均产卵。蜗牛本身既当爹又当娘。',
            html_text:'<h4 class="atitle">两只蜗牛艰难又浪漫的一吻</h4>' +
            '<p>这是国外一摄影师拍摄到的，看到这一幕，还真有爱！于是想查查蜗牛是哪科的，百度科普了一下，原来蜗牛是蜗牛是陆生贝壳类软体动物，关键是蜗牛雌雄同体，异体交配，雌雄均产卵。蜗牛本身既当爹又当娘。</p>' +
            '<p><img src="../../static/img/share04.jpg" alt="两只蜗牛艰难又浪漫一吻" width="90%"/></p>' +
            '<p>关于两只蜗牛相遇，该这般解释：两个蜗牛相遇的时候，互相用触角接触，然后头和头相对，身体并连，彼此生殖腔的位置相接...</p>' +
            '<p>哈哈，看不下去了，一点也不浪漫....</p>'
          },
          {
            title: '用HTML5 Canvas做什么？Canvas有没有实用性?',
            logo: require('../assets/images_jiuye/share05.gif'),
            txt: '什么是Canvas？最近在国外问答网站Quora上，许多开发者对于HTML5 Canvas元素的实用性进行了一系列探讨。',
            html_text:'<h4 class="atitle">用HTML5 Canvas做什么？Canvas有没有实用性?</h4> '+
            '<p class="centent-txt 76">什么是Canvas？最近在国外问答网站Quora上，许多开发者对于HTML5 Canvas元素的实用性进行了一系列探讨。Canvas非常灵活，能够很好地融合JavaScript代码并在浏览器内绘制华丽的图形。Canvas究竟具体能做些什么？3D3R公司创始人兼CEO Ohad Eder-Pressman的答案十分精彩，我们对其文字进行了翻译和整理，不妨一起来看一看。Canvas元素是HTML5的一部分，允许脚本语言动态渲染位图像。Canvas由一个可绘制地区HTML代码中的属性定义决定高度和宽度。JavaScript代码可以访问该地区，通过一套完整的绘图功能类似于其他通用二维的API，从而生成动态的图形。1. 游戏：毫无疑问，游戏在HTML5领域具有举足轻重的地位。HTML5在基于Web的图像显示方面比Flash更加立体、更加精巧，Ohad认为运用Canvas制作的图像能够令HTML5游戏在流畅度和跨平台方面发挥更大的潜力。2. 图表制作：图表制作时常被人们忽略，但无论企业内部还是企业间交流合作都离不开图表。现在一些开发者使用HTML/CSS完成图标制作，但Ohad认为大家完全可以用Canvas来实现。当然，使用SVG（可缩放矢量图形）来完成图表制作也是非常好的方法。3. banner广告：Flash曾经辉煌的时代，智能手机还未曾出现。现在以及未来的智能机时代，HTML5技术能够在banner广告上发挥巨大作用，用Canvas实现动态的广告效果再合适不过。4. 模拟器：Ohad认为，无论从视觉效果还是核心功能方面来说，模拟器产品可以完全由JavaScript来实现。5. 远程计算机控制：Canvas可以让开发者更好地实现基于Web的数据传输，构建一个完美的可视化控制界面。6. 字体设计：对于字体的自定义渲染将完全可以基于Web，使用HTML5技术进行实现。7. 图形编辑器：Ohad预测，图形编辑器将能够100%基于Web实现。8. 其他可嵌入网站的内容：类似图表、音频、视频，还有许多元素能够更好地与Web融合，并且不需要任何插件。Ohad呼吁大家继续挖掘Canvas的潜力，运用HTML5技术创造更多价值。 </p>'
          }
        ]
      }
    }
  }
</script>


