<template>
  <div class="knowMain">
    <h4 class="title">{{$route.params.know[getIndex].title}}</h4>
    <div class="news-text" v-html="content"></div>
    <div class="page" @click="pre" v-if="getIndex>0">上一个分享:{{$route.params.know[getIndex-1].title}}</div>
    <div class="page" v-else>已经到顶了</div>
    <div class="page" @click="next" v-if="getIndex<$route.params.know.length-1">下一个分享:{{$route.params.know[getIndex+1].title}}</div>
    <div class="page" v-else>已经到底了</div>
    <div @click="back" class="news-back">返回</div>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        getIndex:this.$route.params.index,
        content:this.$route.params.know[this.$route.params.index].txt
      }
    },
    methods:{
      back(){
        this.$router.go(-1)
      },
      pre(){
        if(this.getIndex>0){
          this.getIndex--;
          this.content=this.$route.params.know[this.getIndex].txt
        }
      },
      next(){
        if(this.getIndex<this.$route.params.know.length-1){
          this.getIndex++;
          this.content=this.$route.params.know[this.getIndex].txt
        }
      }
    }
  }
</script>
<style>
  h4{
    line-height: 1.8rem;
    font-size: 1.2rem;
    color: #080d10;
  }
.knowMain .news-text{
  margin-top: 0.8rem;
  color: #333;
  font-size: 1rem;
  line-height: 1.2rem;
  text-align: left;
  padding: 0 1rem;
}
  .news-back{
    color: #fff;
    width:3rem;
    height:1.5rem;
    font-size: 1rem;
    line-height: 1.5rem;
    background: #c11920;
    text-align: center;
    margin: 1rem auto;
  }
  .page{
      margin-top: 0.8rem;
      color: #999;
      font-size: 1.2rem;
      line-height: 1.4rem;
    }
</style>



