<template>
    <div>
      <img src="../assets/images_shizi/know.jpg" class="kc-banner">
      <!--<mt-loadmore :top-method="loadTop" :bottom-method="loadBottom"  ref="loadmore">-->
        <ul class="kc-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
          <li v-for="(li,index) in know" >
            <router-link :to="{name:'KnowMain',params:{know,index}}">
              <div class="text">
                <h3>{{li.title}}</h3>
                <p>{{li.txt}}</p>
              </div>
              <div class="line"></div>
            </router-link>
          </li>
          <mt-spinner type="fading-circle" color="rgb(100, 100, 100)" v-show="block"></mt-spinner>
        </ul>
      <!--</mt-loadmore>-->
    </div>
</template>

<script>
  import { InfiniteScroll } from 'mint-ui';
  import { Spinner } from 'mint-ui';
  export  default {
    data() {
      return{
        off:true,
        goodsData :require('../../static/know.json'),
        know:[require('../../static/know.json')[0],require('../../static/know.json')[1],require('../../static/know.json')[2],require('../../static/know.json')[3],require('../../static/know.json')[4]],
        block:true
      }
    },
    methods:{
      loadMore() {
        if(this.off==true){
          this.off=false;
          if(this.know.length<this.goodsData.length){
            this.loading = true;
            setTimeout(() => {
              this.know.push(this.goodsData[this.know.length]);
              this.loading = false;
              this.off=true;
            }, 2500);
          }else{
            this.loading = false;
            this.off=true;
            this.block=false;
          }
        }
      }
    }
  }
</script>


<style>
  .kc-list .line{position: absolute;left: 0.8rem;  bottom: -0.3rem;  width: 23.4rem;  height: 0.6rem;background: url("../assets/images_home/tupian_08.png") repeat-x}
  .kc-banner{width: 100%;}
  .kc-list li{display: flex;padding: 0.8rem;margin: 0.3333333rem 0;position: relative}
  .kc-list li img{width: 6.66666667rem; height: 6.66666667rem; margin-right: 0.46666667rem;  border: 1px solid #cccccc;}
  .kc-list li .text{text-align: center;font-size: 0.4rem;line-height: 0.66666667rem;padding: 0 0.8rem 0 1rem;box-sizing: border-box}
  .kc-list li h3{ margin-bottom: 0.66666667rem; font-size: 1rem; margin-top: 0.833333rem;text-align: left}
  .kc-list li p {
    text-align: left;
    line-height: 1.46666667rem;
    font-size: 0.8rem;
    color: #adadad;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }
  .mint-spinner-fading-circle{
    margin: 0 auto;
    text-align: center;
  }
</style>



