<template>
  <div id="footer">
    <ul>
      <li>
        <router-link :to="{path:'/search'}">
          <div class="icon"><img src="../assets/images_home/tab_iocn_66.png"></div>
          <span>在线搜索</span>
        </router-link>
      </li>
      <li>
        <router-link :to="{path:'/login'}">
          <div class="icon"><img src="../assets/images_home/tab_iocn_62.png"></div>
          <span>用户登录</span>
        </router-link>
      </li>
      <li>
        <a class="call" @click="tel">
          <div class="icon"><img src="../assets/images_home/tab_iocn_64.png"></div>
          <span>电话咨询</span>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
  import '../../static/css/hf.css'
  import '../../static/css/reset.css'
  import { MessageBox } from 'mint-ui';
  export default {
    data(){
      return {
        num:'10086'
      }
    },
    methods:{
      tel(){
        MessageBox({
          title: '',
          message: this.num,
          confirmButtonText:'拨打电话'
        }).then(action => {
          window.location.href='tel://'+this.num
        });
      }
    }
  }
</script>

