<template>
  <div id="app">
    <HeadTop></HeadTop>
    <transition name="animate"><router-view class="content" keep-alive></router-view></transition>
    <FootEnd></FootEnd>
  </div>
</template>

<script>
  import HeadTop from './components/HeadTop'
  import FootEnd from './components/Foot'
export default {
  components:{HeadTop,FootEnd}
}
</script>

<style>
  .content{transition:0.3s; position: absolute;width:100%;top: 5.93333333rem;bottom:3.266666667rem;}
  .animate-enter{
      transform: translate(100%,0);opacity: 0;
  }
  .animate-leave-active{
    transform:translate(-100%,0);opacity: 0;
  }
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /*margin-top: 60px;*/
}
</style>



