<template>
  <div>
    <h3 class="icon-title">{{$route.params.icon[getIndex].title}}</h3>
    <p class="icon-text" v-html="content"></p>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        getIndex: this.$route.params.index,
        content: this.$route.params.icon[this.$route.params.index].text
      }
    }
  }
</script>

<style>
  .icon-title{
    margin-top: 1rem;
    line-height: 1.8rem;
    height:1.8rem;
    font-size: 1.4rem;
    color: #080d10;
    font-weight: bold;
  }
  .icon-text{
    padding: 1rem;
    font-size: 1rem;
    text-align: left;
    text-indent: 1rem;
    line-height: 1.8rem;
  }
</style>



