<template>
  <div class="shareMain">
    <div class="news-text" v-html="content"></div>
    <div class="page" @click="pre" v-if="getIndex>0">上一个分享:{{$route.params.share[getIndex-1].title}}</div>
    <div class="page" v-else>已经到顶了</div>
    <div class="page" @click="next" v-if="getIndex<$route.params.share.length-1">下一个分享:{{$route.params.share[getIndex+1].title}}</div>
    <div class="page" v-else>已经到底了</div>
    <div @click="back" class="news-back">返回</div>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        getIndex:this.$route.params.index,
        content:this.$route.params.share[this.$route.params.index].html_text
      }
    },
    methods:{
      back(){
        this.$router.go(-1)
      },
      pre(){
        if(this.getIndex>0){
          this.getIndex--;
          this.content=this.$route.params.share[this.getIndex].html_text
        }
      },
      next(){
        if(this.getIndex<this.$route.params.share.length-1){
          this.getIndex++;
          this.content=this.$route.params.share[this.getIndex].html_text
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .news-title{
    line-height: 1.8rem;
    height:1.8rem;
    font-size: 1.2rem;
    color: #080d10;
  }
  .shareMain p{
    text-indent: 1rem;
    padding: 0.5rem 1rem;
    text-align: left;
  }
  .news-time{
    line-height: 1.2rem;
    height:1.2rem;
    color:#999;
    font-size:0.8rem;
  }
  .atitle{
    line-height: 1.8rem;
    font-size: 1.2rem;
    color: #080d10;
  }

  .news-text{
    margin-top: 0.8rem;
    color: #333;
    font-size: 0.8rem;
    line-height: 1rem;
  }
  .news-back{
    color: #fff;
    width:3rem;
    height:1.5rem;
    font-size: 1rem;
    line-height: 1.5rem;
    background: #c11920;
    text-align: center;
    margin: 1rem auto;
  }
  .page{
    margin-top: 0.8rem;
    color: #999;
    font-size: 1.2rem;
    line-height: 1.4rem;
  }
</style>



