import Vue from 'vue'
import Router from 'vue-router'
import Know from '../page/know'
import Home from '../page/home'
import Share from '../page/share'
import Our from '../page/our'
import About from '../page/about'
import Diary from '../page/diary'
import Register from '../page/register'
import Login from '../page/login'
import News from '../page/news'
import News2 from '../page/news2'
import Icon from '../page/icon'
import Search from '../page/search'
import ShareMain from '../page/shareMain'
import KnowMain from '../page/KnowMain'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/Know',
      component: Know
    },
    {
      path: '/Home',
      component: Home
    },
    {
      path: '/About',
      component: About
    },
    {
      path: '/Share',
      component: Share
    },
    {
      path: '/Our',
      component: Our
    },
    {
      path: '/Diary',
      component: Diary
    },
    {
      path: '/Register',
      component: Register
    },
    {
      path: '/Login',
      component: Login
    },
    {
      name:'News',
      path: '/News',
      component: News
    },
    {
      name:'News2',
      path: '/News2',
      component: News2
    },
    {
      name:'Icon',
      path: '/Icon',
      component: Icon
    },
    {
      path: '/Search',
      component: Search
    },
    {
      name:'ShareMain',
      path: '/ShareMain',
      component: ShareMain
    },
    {
      name:'KnowMain',
      path: '/KnowMain',
      component: KnowMain
    }
  ]
})



