<template>
  <div class="login">
    <mt-field label="手机" placeholder="请输入手机号" type="number" v-model="phone"></mt-field>
    <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
    <mt-button type="danger" @click="sub">确认</mt-button>
    <router-link :to="{path:'/register'}"><mt-button type="danger">注册</mt-button></router-link>
  </div>
</template>

<script>
  import { MessageBox } from 'mint-ui';
  export default {
    data(){
      return {
        phone:'',
        password:''
      }
    },
    methods:{
      sub(){
        if(this.$store.state.aa.people[this.name]==this.phone && this.$store.state.aa.people[this.psw]==this.password){
          MessageBox({
            title: '',
            message: '登录成功',
            confirmButtonText:'确定'
          }).then(action => {
            this.$router.push('/home');
          });
        }else{
          this.phone='';
          this.password='';
          MessageBox({
            title: '',
            message: '请输入正确信息',
            confirmButtonText:'确定'
          });
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .mint-cell-wrapper{
    text-align: left;
  }
</style>




