<template>
  <div>
    <img src="../assets/images_shizi/diary.jpg" class="sz-banner">
    <ul class="sz-list">
      <li v-for="li in diary">
        <div class="text">
          <h3>{{li.title}}</h3>
          <p>{{li.txt}}</p>
        </div>
        <div class="line"></div>
      </li>
    </ul>
  </div>
</template>

<script>
  export  default {
    data() {
      return{
        diary:[
          {
            title: '2017-10-14',
            txt: '出来混，迟早会还的'
          },
          {
            title: '2017-10-13',
            txt: '不是所有鱼都生活在同一片海洋里。'
          },
          {
            title: '2017-10-12',
            txt: '当你老了，白发苍苍，睡意朦胧，在炉前打盹，慢慢吟诵，梦见你当年的双眼，那柔美的光芒与青幽的晕影。'
          },
          {
            title: '2017-10-11',
            txt: '你问我那么拼干嘛，我说我是一个惜时惜命的人。'
          },
          {
            title: '2017-10-10',
            txt: '你若盛开。清风徐来。'
          },
          {
            title: '2017-10-09',
            txt: '年轻吗？不要紧，听过几首歌，爱过几个人，就老了。'
          },
          {
            title: '2017-10-08',
            txt: '已过了喜欢炫耀的年纪。遇到喜欢的事情就努力去做。'
          },
          {
            title: '2017-10-07',
            txt: '突然没电是一件多么难过的事情。'
          },
          {
            title: '2017-10-06',
            txt: '总是有人和我说，总有一天，你终将会变成你讨厌的人，谢谢那些人的吉言，我讨厌有钱人。'
          },
          {
            title: '2017-10-05',
            txt: '天寒地冻，忘穿秋裤，无处话凄凉。'
          },
          {
            title: '2017-10-01',
            txt: '曾经梦想仗剑走天涯，可谁曾想，一入IT深似海，曾经梦想赴沧海。'
          }
        ]
      }
    }
  }
</script>


<style>
  .sz-list .line{position: absolute;left: 0.8rem;  bottom: -0.3rem;  width: 23.4rem;  height: 0.6rem;background: url("../assets/images_home/tupian_08.png") repeat-x}
  .sz-banner{width: 100%;height:6.66666667rem;}
  .sz-list li{display: flex;padding: 0.8rem;margin: 0.3333333rem 0;position: relative}
  .sz-list li .text{text-align: center;font-size: 0.4rem;line-height: 0.66666667rem;padding: 0 0.8rem 0 1rem;box-sizing: border-box}
  .sz-list li h3{ margin-bottom: 0.66666667rem; font-size: 1rem; margin-top: 0.833333rem;text-align: left}
  .sz-list li p{
    text-align: left;
    line-height: 1.46666667rem;
    font-size: 0.8rem;
    color: #adadad;
  }
</style>



