/* harmony default export */ __webpack_exports__["a"] = ({
  data: function data() {
    return {
      share: [{
        title: '程序员请放下你的技术情节，与你的同伴一起进步',
        logo: __webpack_require__("LPID"),
        txt: '如果说掌握一门赖以生计的技术是技术人员要学会的第一课的话， 那么我觉得技术人员要真正学会的第二课，不是技术，而是业务、交流与协作，学会关心其他工作伙伴的工作情况和进展。',
        html_text: '<h4 class="atitle">程序员请放下你的技术情节，与你的同伴一起进步</h4>' + '<p>如果说掌握一门赖以生计的技术是技术人员要学会的第一课的话， 那么我觉得技术人员要真正学会的第二课，不是技术，而是业务、交流与协作，学会关心其他工作伙伴的工作情况和进展。</p>' + '<p>为什么这么说呢？ 因为技术人员太容易陷入&ldquo;孤岛&rdquo;状态，更注重自己的工作任务的完成，忽视其他工作伙伴的工作，甚至一无所知。 我就一直犯这样的错误。我敢说，对内心我还是比较明白清楚的，但是对外面所发生的事情实在是知之甚少，这不是好的状态。 一个开明、开放的程序员不应该囿于自己狭隘的小天地，而是更广阔地去看待工作和职业，和同伴一起进步和成功。</p>' + '<p><strong>为什么要关心业务？</strong></p>' + '<p>很多技术人员都立志成为系统架构师， 那是编程领域的&ldquo;圣杯级职业&rdquo;。问题是， 系统架构本身是为了业务需求和扩展而服务的，必须充分理解业务需求和未来的发展趋势，深入理解系统所涉及的数据及分布， 才能作出更可靠的设计决策。如果对业务知之甚少，以为仅靠书上说的那一套，或者以前的零碎经验，就能够胜任的话，那这个项目多半要失败的。 因此，即使立志要成为系统架构师，也要对业务有很深的理解。</p>' + '<p>其次， 技术的发挥必须有用武之地。如果没有足够强度和有挑战性的业务需求和扩展， 足够多的问题的磨炼，技术的提升又从何谈起？ 仅仅靠阅读那些技术书籍吗？ 那只能提供一个指导的作用罢了；真正还是要在实战中得到提高。</p>' + '<p>如果一个技术人员对业务不感兴趣，只对技术感兴趣， 那会发生什么事情呢？ 他将只能满足于使用自己所熟悉的技术去完成上面分派下来的任务； 也许今天是做一个系统A， 明天是做另外一个类似的系统B。 他对系统涉及的各种业务都不甚了解，也不清楚行业领域的发展状况， 就只能局限于使用技术来做各种具体的功能，无法提出中肯的建议。他的职业发展将严重耦合于所掌握的技术。如果该技术保持比较长久的生命力，那么， 他还能兵来将挡水来土淹， 但无论如何，也只能局限于成为这门技术的&ldquo;高级工程师&rdquo;而已；如果该技术开始被淘汰，那就悲剧了。</p>' + '<p>換一种方式。如果一个技术人员很关心业务。 那么，经过一段时间的磨练之后，他能够提出中肯的意见，知晓行业领域的发展状况， 在深入理解业务的基础上，同时发展相应的技术专长， 做一个在业务和技术方面并行发展的技术人员。 他的职业发展将不是完全耦合于特定的某种开发技术，而具备了更大的灵活性。</p>' + '<p>你知道吗？ 三国时期，我最佩服的是徐元直先生。 他是一个技术和业务同样精通的人才； 技术方面， 作战思想丰富， 业务方面， 实际指挥作战和应变能力都很强， 这样的人才， 难怪即使不献一策， 曹操这样的人物也愿意留他在帐下， 让他无功受禄，&mdash;&mdash; 能够不让他为竞争对手所用就已经是大功一件了。</p>' + '<p><strong>放下技术情结</strong></p>' + '<p>技术人员要学会放下心中的&ldquo;技术情结&rdquo;。 因为我也是技术人员，也有技术崇拜的倾向， 也能够感受到这种情结带来的益处和束缚。 &ldquo;技术情结&rdquo; 表现在哪里呢？ 你期望能够尽可能多尽可能深入地掌握各种技术，这给你带来一种很好的安全感，因为有了强有力的依靠，&mdash;&mdash; 你对自己的能力充分自信； 但也束缚了一个人的发展： 他的内心容易更加依赖于自己的能力，而对别人的能力抱有怀疑，难以与别人形成优势互补和良好搭档，很难发挥出超出自己能力的能量。</p>' + '<p>是的， 你学了 C, java, python , Lisp , ...., 还可以列出更长，你还想掌握并发编程，软件架构等等，你的同事却只会C， java 或 python , 会敲入一些命令， 这样你就舒服了吗？ 感到优越了吗？ 高枕无忧了吗？ 成为核心骨干了吗？</p>' + '<p>技术是学无止境的， 一个人的精力却有限，就算一个人在某个领域里非常精通，那么同时也可能意味着这个人在其它方面是孤陋寡闻，&ldquo;技术牛人&rdquo; 和 &ldquo;科学大师&rdquo; 一样， 可能只是一个美妙的光环，是程序员给自己套上的铁链。不要妄想一揽子全抓在手里， 确立自己的专长， 善于与别人优势互补，良好协作才是。敢于舍弃 &ldquo;技术情结&rdquo; 给自己带来的安全感， 才能走出更广阔的空间。</p>' + '<p><strong>技术人员的通病</strong></p>' + '<p>大多数技术人员，包括我在内， 都不甚明了自己究竟能够利用计算机做什么。 我们只是年复一年日复一日地学习和使用某种编程语言和技术来写程序，以为这就是利用计算机的唯一的正统方式。你用过MATLAB吗， 一种很强悍的科学计算软件？ 那里一条命令， 就顶一个编程人员几个月的努力。作为一名计算机专业人士， 学习了那么多专业知识，难道仅仅只是为了掌握一两门编程语言和技术来写点程序养家糊口吗？ 如果一个程序员懂得去使用一些专业软件，学习一些信息处理、统计分析方法， 那么， 他所能提供的价值可能远远超过一个普通开发者所能提供的价值。具备编程能力和对编程技术的领悟，是技术人员拥有的特别优势， 但并不意味着一个技术人员能够干的活就只是编程。只是，大多数技术人员，由于各种原因，就把自己定位在一名普通的程序开发人员身上， 跳不出&ldquo;开发人员&rdquo;的视角。</p>' + '<p>我们总是沉迷于重新发明轮子，以及被迫重新发明轮子，用不同的编程语言，或者用同一套技术框架，搭完了管理系统ABCDEFG，再搭HIJKLMN，以及OPQRSTUVWXYZ， 感觉很有成就感吧？ 不懂尊重和利用别人已有的工作成果，低水平重复建设，耗费大量的时间、精力、人力和资源成本去做那些没有太大意义的事情； 总是沉迷于争论语言、技术之间的孰优孰劣，却甚少关心哪些事当做不当做，甚少关心做那些关键重要的事情有哪些方案以及孰优孰劣；总是沉迷于某款科技产品的宣传和特性，甚少关心环境问题和儿童失学问题，如果说程序员有什么可怜可恨之处，那绝对不在于这一族自甘被孤立，难以为人所理解， 而在于他们自身的心态就将自己锁在了井底之中，还自以为很与众不同。</p>' + '<p>假设你不再为公司编程，而为自己写程序， 你知道自己该干些什么吗？ 你会感到迷茫吗？ 如果我们连自己该干什么能干什么都不清楚， 那么徒有一身武艺，又有什么用呢？</p>' + '<p>大多数技术人员，包括我在内，都还不懂得主动与工作伙伴良好协作，只是因为工作关系而不自觉地交流和协作。不信，在你工作之余的时候，你会主动邀请别人一起来编程，共同去做一个有用的产品， 还是埋头去学习技术，孤立地去做一些技术实验，来掌握所谓的某门开发技术？</p>' + '<p>一个技术人员对于另一个技术人员的认可往往起始于对其技术能力的是否认可。技术人员面试的时候通常会更关注： 他掌握了哪些技术？ 对这些技术的掌握程度如何？ 而不是这个人利用自己的技术能力和所学做了哪些影响卓著的事情。这就导致了： 技术人员往往更关注自己的技术能力的发展，而忽视其他能力的培养。</p>' + '<p>因此，大多数技术人员，包括我在内， 都有四个通病： 一，不清楚自己究竟能够利用计算机做什么，不懂得如何充分利用计算机的真正威力； 二. 不能主动与别人优势互补和良好协作； 三。放不下技术情结； 四. 局限于狭隘的自我优越感，以及伴随而生的强迫症。这四个通病导致一个人永远局限于只见一叶不见林的狭隘视角。</p>' + '<p><strong>真正优秀的技术人才</strong></p>' + '<p>作为一名技术人员，你对公司的业务发展能够提出自己的建议吗？ 你有能力说服工作伙伴和主管来采纳你的提议吗？ 能够集思广益，融众人之所长吗？ 我认为，一个真正优秀的技术人才，应当具备这种多方面的能力，除了技术专长，他具备说服能力，集思广益， 能够有力地推动事情的发展。企业也应当提供机会，让技术人员参与更广阔的工作，而不仅仅是写代码和完成需求功能。</p>' + '<p>我相信，从企业聘用人才的角度来说，它们更看重一名技术人才究竟产生了怎样的影响。只是当大量技术人员没有什么可证明自己做过有影响力的事情的情况下，才不得不求其次，去聘用那些在技术能力上有更高造诣的技术人员； 从技术人员的角度来说，因为他没有什么可以证明自己确实做过有影响力的事情，只好倚靠有限的技术水平为自己寻一个差强人意的安身之所。我觉得， 那些没有做过有影响力的事情的技术人员，相比那些利用自己所学做过有价值的事情的人来说，其职业高度就已经低了一个档次，尽管后者可能在技术能力上要差一些。</p>' + '<p>事实上，我敢断言： 中国不缺技术能力优秀的人才，虽然技术级别与国外还差一两个档次， 但还是人才济济的。确实有很多人依然努力成为最好的技术人才，致力于引进国外先进技术。中国缺少的是那些懂得利用计算机能力创造价值、服务和效益的技术人才。 这种技术人才必须能够放下技术情结，以更开放、开明的心态去与其他的人良好协作，优势互补， 一个真正优秀的产品往往是多个人共同协作完成，孤胆英雄的年代已经过去。即便是一个黑客，如果他能与另一个黑客强强联合， 也能产生出比自己一个人更强大的能量。要知道，苹果至少也是由乔布斯和沃兹尼亚克两个人初创起来的。</p>' + '<p><strong>要一起进步和成功</strong></p>' + '<p>最后， 如果一个技术人员通过重重磨难终于成为了很牛级的人物，而他的亲友早已埋没于尘世中，过着一般的日子， 他的成功又有多大意义呢？ 应该聚合所有人的才智和经验，一起创造共赢的结局才对，缺了谁都不行。人生苦短，正是因为一路上有了亲友的陪伴， 才不会寂寞， 人生才不至于苍白。不要只顾着自己一个人往前走路， 把亲友们抛在看不见的后面。</p>'
      }, {
        title: '每人的生命里都有一只碗',
        logo: __webpack_require__("8fRj"),
        txt: '有一个年轻人去买碗，来到店里他顺手拿起一只碗，然后依次与其它碗轻轻碰击，碗与碗之间相碰时立即发出沉闷、浑浊的声响，他失望地摇摇头。',
        html_text: '<h4 class="atitle">每人的生命里都有一只碗</h4>' + '<p>有一个年轻人去买碗，来到店里他顺手拿起一只碗，然后依次与其它碗轻轻碰击，碗与碗之间相碰时立即发出沉闷、浑浊的声响，他失望地摇摇头。 然后去试下一只碗&hellip;&hellip;他几乎挑遍了店里所有的碗，竟然没有一只满意的，就连老板捧出的自认为是店里碗中精品也被他摇着头失望地放回去了。</p>' + '<p>老板很是纳闷，问他老是拿手中的这只碗去碰别的碗是什么意思？他得意地告诉老板，这是一位长者告诉他的挑碗的诀窍，当一只碗与另一只碗轻轻碰撞时，发出清脆、悦耳声响的，一定是只好碗。老板恍然大悟，拿起一只碗递给他，笑着说：&ldquo;小伙子，你拿这只碗去试试，保管你能挑中自己心仪的碗&rdquo;。</p> ' + '<p><img src="../../static/img/share02.png" alt="每人的生命里都有一只碗" width="90%"/></p> <p>他半信半疑地依言行事。奇怪！他手里拿着的每一只碗都在轻轻地碰撞下发出清脆的声响，他不明白这是怎么回事，惊问其详。</p> <p>老板笑着说，道理很简单，你刚才拿来试碗的那只碗本身就是一只次品，你用它试碗那声音必然浑浊，你想得到一只好碗，首先要保证自己拿的那只也是只好碗&hellip;&hellip;</p> <p>就像一只碗与另一只碗的碰撞一样，一颗心与另一颗心的碰撞，需要付出真诚，才能发出清脆悦耳的响声。自己带着猜忌、怀疑甚至戒备之心与人相处，就难免得到别人的猜忌与怀疑。其实每个人都可能成为自己生命中的&ldquo;贵人&rdquo;，前提条件是，你应该与人为善。</p> ' + '<p>你付出了真诚，就会得到相应的信任，你献出爱心，就会得到尊重。反之，你对别人虚伪、猜忌甚至嫉恨，别人给你的也只能是一堵厚厚的墙和一颗冷漠的心。每个人的生命里都有一只碗，碗里盛着善良、信任、宽容、真诚，也盛着虚伪、狭隘、猜忌、自私&hellip;&hellip;请剔除碗里的杂质，然后微笑着迎接另一只碗的碰撞，并发出你们清脆、爽朗的笑声吧！</p> ' + '<p>做最好的自己，才能碰见最好的别人。</p> '
      }, {
        title: '犯错了怎么办？',
        logo: __webpack_require__("MAK8"),
        txt: '看到昔日好友发了一篇日志《咎由自取》他说他是一个悲观者，感觉社会抛弃了他，脾气、性格在6年的时间里变化很大，很难适应这个社会。',
        html_text: '<h4 class="atitle">犯错了怎么办？</h4>' + '<p>看到昔日好友发了一篇日志《咎由自取》他说他是一个悲观者，感觉社会抛弃了他，脾气、性格在6年的时间里变化很大，很难适应这个社会。人生其实就是不断犯错的过程，在这个过程中不断的犯错，不断的吸取教训，不断的成长。也许日子里的惊涛骇浪，不过是人生中的水花摇晃，别用显微镜放大你的悲伤。</p> <p><span style="color: rgb(0, 0, 0);"><strong>龙背墙的传说</strong></span></p>' + '<p>龙背墙是由孙红雷、郭富城、余男、邓家佳主演的《全民目击》里面的一个传说，关于一个父爱和救赎的神话。影片讲的是一个富二代因为酒后撞死人，父亲为了挽救女儿 ，策划了一系列伪证，哪怕最后替女儿坐牢。&ldquo;什么都不重要，只要她好，就好。&rdquo;&ldquo;我愿用一生年华，换她十五年光阴。&rdquo;&ldquo;你若是父亲，也一定会这样做。&rdquo;这个故事 ，让我理解了很多，这电影让人感同身受之处在于，从一开始，就没有完美的结局，总有人会犯错会受伤，总有人会去承担去背负。</p> ' + '<p>传说龙背墙不是一面墙，而是一座山。南海龙王老来得子，小龙王淘气任性，到处惹祸。直到有一天，他闯了大祸，南海龙王为了救儿子，他冒替小龙王，趴在盘龙山下， 接受雷电的击打。眼看父亲被烧的遍体鳞伤，奄奄一息，小龙王悔恨愧疚，他一下冲出来，要承担这个惩罚，龙王为了阻止儿子，便一头撞向身旁的金刚壁，当场死去。&ldquo; 养不教，父之过&ldquo;，南海龙王认为自己死得其所，死后，他的尸体便化作龙背山，经过这场灾难之后，小龙王幡然醒悟，终生恪守本分，与人为善。后人将龙背山改称为龙背墙，是因为这面墙，挡住了小龙王所有的罪行，故此，后人称为龙背墙。&nbsp;</p> ' + '<p><span style="color: rgb(0, 0, 0);"><strong>物是人非事事休</strong></span></p> <p><strong>物也非，人也非，事事非，往日不可追。</strong>记得小时候我带弟弟去自家鱼塘钓鱼，拿着鱼竿，拿着桶，我很熟练轻松的跨过那不到半米的沟渠，还没来得及我提醒他，只听&ldquo;扑通&rdquo;一声，我回头，弟弟已经掉进了鱼塘，我站在原地不知所措，大声的哭喊。幸好我爸在家听见我哭，问我怎么回事，我说弟弟掉下去了，据我爸回忆说，当时就看到鱼塘冒一圈圈的水，衣服都来不及脱，大冬天的直接下去捞人，由于泥太多，我爸只能托举着我弟身体等到人来救援...救上来后，因为呛着水、做人工呼吸、放牛背、盖被子..所有人都绝望的认为活不了的时候，弟弟却奇迹般的醒了，虽说是我爸救了他，但是我也有很大的责任，要是我俩不去钓鱼，又或者是他走在前面，就不会发生这样的事情，现在想想还很后怕，如果不是那一刻的哭喊，如果不是我爸即时赶到，如果不是有路人帮忙，我怕这辈子都会内疚下去....还记得有一次弟弟吃了马蹄莲的花蕊，后来因为花蕊中毒，嘴又黑又肿进了医院，每次回忆这事儿的时候我老笑话他，可不曾想，他说是我骗他说那花蕊好吃...哈哈，反正我是不记得了。</p> ' + '<p><span style="color: rgb(255, 0, 0);"><strong>写在最后：</strong></span></p> <p>犯错不需要勇气，你甚至有很多理由来解释来弥补，但是却需要很大的勇气来面对，必须得面对，很现实的面对。 '
      }, {
        title: '两只蜗牛艰难又浪漫的一吻',
        logo: __webpack_require__("sPUR"),
        txt: '这是国外一摄影师拍摄到的，看到这一幕，还真有爱！于是想查查蜗牛是哪科的，百度科普了一下，原来蜗牛是蜗牛是陆生贝壳类软体动物，关键是蜗牛雌雄同体，异体交配，雌雄均产卵。蜗牛本身既当爹又当娘。',
        html_text: '<h4 class="atitle">两只蜗牛艰难又浪漫的一吻</h4>' + '<p>这是国外一摄影师拍摄到的，看到这一幕，还真有爱！于是想查查蜗牛是哪科的，百度科普了一下，原来蜗牛是蜗牛是陆生贝壳类软体动物，关键是蜗牛雌雄同体，异体交配，雌雄均产卵。蜗牛本身既当爹又当娘。</p>' + '<p><img src="../../static/img/share04.jpg" alt="两只蜗牛艰难又浪漫一吻" width="90%"/></p>' + '<p>关于两只蜗牛相遇，该这般解释：两个蜗牛相遇的时候，互相用触角接触，然后头和头相对，身体并连，彼此生殖腔的位置相接...</p>' + '<p>哈哈，看不下去了，一点也不浪漫....</p>'
      }, {
        title: '用HTML5 Canvas做什么？Canvas有没有实用性?',
        logo: __webpack_require__("7MXm"),
        txt: '什么是Canvas？最近在国外问答网站Quora上，许多开发者对于HTML5 Canvas元素的实用性进行了一系列探讨。',
        html_text: '<h4 class="atitle">用HTML5 Canvas做什么？Canvas有没有实用性?</h4> ' + '<p class="centent-txt 76">什么是Canvas？最近在国外问答网站Quora上，许多开发者对于HTML5 Canvas元素的实用性进行了一系列探讨。Canvas非常灵活，能够很好地融合JavaScript代码并在浏览器内绘制华丽的图形。Canvas究竟具体能做些什么？3D3R公司创始人兼CEO Ohad Eder-Pressman的答案十分精彩，我们对其文字进行了翻译和整理，不妨一起来看一看。Canvas元素是HTML5的一部分，允许脚本语言动态渲染位图像。Canvas由一个可绘制地区HTML代码中的属性定义决定高度和宽度。JavaScript代码可以访问该地区，通过一套完整的绘图功能类似于其他通用二维的API，从而生成动态的图形。1. 游戏：毫无疑问，游戏在HTML5领域具有举足轻重的地位。HTML5在基于Web的图像显示方面比Flash更加立体、更加精巧，Ohad认为运用Canvas制作的图像能够令HTML5游戏在流畅度和跨平台方面发挥更大的潜力。2. 图表制作：图表制作时常被人们忽略，但无论企业内部还是企业间交流合作都离不开图表。现在一些开发者使用HTML/CSS完成图标制作，但Ohad认为大家完全可以用Canvas来实现。当然，使用SVG（可缩放矢量图形）来完成图表制作也是非常好的方法。3. banner广告：Flash曾经辉煌的时代，智能手机还未曾出现。现在以及未来的智能机时代，HTML5技术能够在banner广告上发挥巨大作用，用Canvas实现动态的广告效果再合适不过。4. 模拟器：Ohad认为，无论从视觉效果还是核心功能方面来说，模拟器产品可以完全由JavaScript来实现。5. 远程计算机控制：Canvas可以让开发者更好地实现基于Web的数据传输，构建一个完美的可视化控制界面。6. 字体设计：对于字体的自定义渲染将完全可以基于Web，使用HTML5技术进行实现。7. 图形编辑器：Ohad预测，图形编辑器将能够100%基于Web实现。8. 其他可嵌入网站的内容：类似图表、音频、视频，还有许多元素能够更好地与Web融合，并且不需要任何插件。Ohad呼吁大家继续挖掘Canvas的潜力，运用HTML5技术创造更多价值。 </p>'
      }]
    };
  }
});

