<template>
  <ul>
    <!--<li><img src="../../static/img_star/01.jpg" @click="handleClick">-->
      <!--<mt-popup-->
        <!--v-model="popupVisible"-->
        <!--modal=false>-->
        <!--<child></child>-->
      <!--</mt-popup>-->
    <!--</li>-->
    <li v-for="(item,index) in listimg">
      <img v-lazy="item" @click="handleClick(index)">
      <mt-popup
        v-model="content[index].popupVisible" popup-transition="popup-fade"
        modal=false>
        <h2>{{content[index].title}}</h2>
      </mt-popup>
    </li>
  </ul>
</template>

<script>
  import { Popup } from 'mint-ui';
  export  default {
    data() {
      return {
        listimg: ['../../static/img_star/01.jpg', '../../static/img_star/02.jpg', '../../static/img_star/03.jpg',
          '../../static/img_star/04.jpg', '../../static/img_star/05.jpg', '../../static/img_star/06.jpg',
          '../../static/img_star/07.jpg', '../../static/img_star/08.jpg', '../../static/img_star/10.jpg',
          '../../static/img_star/11.jpeg', '../../static/img_star/12.jpg', '../../static/img_star/13.jpeg',
          '../../static/img_star/14.jpeg', '../../static/img_star/15.jpeg', '../../static/img_star/16.jpeg'
        ],
        content: [
          {title: '这是第一张图片：林允儿', popupVisible: false}, {title: '这是第二张图片：林允儿', popupVisible: false},
          {title: '这是第三张图片：林允儿', popupVisible: false}, {title: '这是第四张图片：林允儿', popupVisible: false},
          {title: '这是第五张图片：林允儿', popupVisible: false}, {title: '这是第六张图片：林允儿', popupVisible: false},
          {title: '这是第七张图片：林允儿', popupVisible: false}, {title: '这是第八张图片：林允儿', popupVisible: false},
          {title: '这是第九张图片：迪亚热巴', popupVisible: false}, {title: '这是第十张图片：迪亚热巴', popupVisible: false},
          {title: '这是第十一张图片：迪亚热巴', popupVisible: false}, {title: '这是第十二张图片：迪亚热巴', popupVisible: false},
          {title: '这是第十三张图片：迪亚热巴', popupVisible: false}, {title: '这是第十四张图片：迪亚热巴', popupVisible: false},
          {title: '这是第十五张图片：迪亚热巴', popupVisible: false}
        ]
      }
    },
    methods:{
      handleClick: function(index) {
        this.content[index].popupVisible = true;
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .our{
    width:100%;
  }
  img{
    width:100%;
  }
  image[lazy=loading] {
    width: 40px;
    height: 300px;
    margin: auto;
  }
  li .mint-popup{
    background: #654321;
    color:#fff;
    padding: 1rem 2rem;
  }
  li .mint-popup h2{
    font-size: 1.2rem;
  }
</style>



